<template>
  <div style="margin: 30px 0 0 0">
    <div class="gonggao bdb_d fix">
      <div class="txtScroll-top l fix zy_cur">
        <div class="l tit">
          <b class="dib vm">当前位置：</b>
        </div>
        <p class="l">
          <router-link to="/index">首页</router-link><span>课程信息</span>
        </p>
      </div>
    </div>

    <div class="zy_contentbox">
      <div class="zy_content fix">
        <div class="zy_left_con">
          <div class="zy_twonav">
            <h2 class="tit">教学管理</h2>
            <ul>
              <li
                v-for="(item, index) in menu"
                :class="{ cur: currpage == item }"
              >
                <a href="javascript:void(0)" @click="loadData(item)">{{
                  item
                }}</a>
              </li>
            </ul>
          </div>
        </div>

        <div class="zy_right_con">
          <div v-if="currpage == '教学视频'">
            <div class="zy_title fix">
              <h2><i></i>教学视频</h2>
            </div>
            <div class="zy_peoplebox">
              <ul>
                <li v-for="(item, index) in list">
                  <a href="javascript:;" @click="toDetail(1, item.id)">
                    <i><img :src="item.photoUrl" /></i>
                    <p>
                      <b>{{ item.title }}</b>
                    </p>
                  </a>
                </li>
              </ul>
            </div>
            <div class="fanye" style="text-align: left">
              <a
                href="#"
                :class="{ customdisable: !page.hasPreviousPage }"
                @click="loadData(page.prePage)"
                >上一页</a
              >
              <div v-for="(item, index) in page.navigatepageNums">
                <a
                  href="#"
                  :class="{ current: item == page.pageNum }"
                  @click="loadData(item)"
                  >{{ item }}</a
                >
              </div>
              <a
                href="#"
                :class="{ customdisable: !page.hasNextPage }"
                @click="loadData(page.nextPage)"
                >下一页</a
              >
              <p>
                <span>共{{ page.pages }}页</span>
              </p>
            </div>
          </div>
          <div v-if="currpage == '教学资料'">
            <div class="zy_title fix">
              <h2><i></i>教学资料</h2>
            </div>
            <div class="zy_peoplebox">
              <ul>
                <li v-for="(item, index) in list1">
                  <a href="javascript:;" @click="toDetail(2, item.id)">
                    <i><img :src="item.coursewareUrl" /></i>
                    <p>
                      <b>{{ item.author }}</b>
                    </p>
                  </a>
                </li>
              </ul>
            </div>
            <div class="fanye" style="text-align: left">
              <a
                href="#"
                :class="{ customdisable: !page1.hasPreviousPage }"
                @click="loadData(page1.prePage)"
                >上一页</a
              >
              <div v-for="(item, index) in page1.navigatepageNums">
                <a
                  href="#"
                  :class="{ current: item == page1.pageNum }"
                  @click="loadData(item)"
                  >{{ item }}</a
                >
              </div>
              <a
                href="#"
                :class="{ customdisable: !page1.hasNextPage }"
                @click="loadData(page1.nextPage)"
                >下一页</a
              >
              <p>
                <span>共{{ page1.pages }}页</span>
              </p>
            </div>
          </div>

          <div v-if="currpage == 'detail'">
            <div class="zy_content fix">
              <div class="zy_conbox">
                <div class="zy_newsxq">
                  <div class="xq_tit bdb_d">
                    <h1>
                      {{ article.title }}
                    </h1>
                  </div>
                  <div class="txt">
                    <video :src="article.videoUrl" controls="controls">
                      您的浏览器不支持 video 标签。
                    </video>
                  </div>
                  <div v-html="article.remark"></div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="currpage == 'detail1'">
            <div class="zy_content fix">
              <div class="zy_conbox">
                <div class="zy_newsxq">
                  <div class="xq_tit bdb_d">
                    <h1>
                      {{ article1.author }}
                    </h1>
                  </div>
                  <div class="txt">
                    <img class="fileDetailImg" :src="'http://127.0.0.1:9091/static/res/'+article1.coursewareUrl"></img>
                  </div>
                  <div class="Flink">
                    <h3>资料文件：</h3>
                    <a class="fileLink" v-for="item in article1.fileInfoList" :key="item.index" :href="item.networkPath">{{item.fileName}}</a>
                  </div>
                  <div v-html="article1.remark"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      currpage: "教学视频",
      menu: ["教学视频", "教学资料"],
      article: {},
      article1: {},
      list: [],
      page: {},
      list1: [],
      page1: {},
    };
  },
  created() {
    this.loadData("教学视频");
  },
  methods: {
    /* loadData: function (page) {
      this.currpage = page;
    }, */
    loadData: function (page) {
      this.currpage = page;
      axios.get("/api/index/video/page", {}).then((d) => {
        if (d.data.code == 200) {
          /* this.currpage = "教学视频"; */
          let result = d.data.data;
          this.page = result;
          this.list = result.list;
        }
      });
      axios.get("/api/index/courseware/page", {}).then((d) => {
        if (d.data.code == 200) {
          /* this.currpage = "教学视频"; */
          console.log(d.data.data);

          let result = d.data.data;
          this.page1 = result;
          this.list1 = result.list;
        }
      });
    },
    toDetail: function (type, id) {
      if (type == 1) {
        axios.get("/api/index/video/get?id=" + id).then((d) => {
          this.currpage = "detail";
          this.article = d.data.data;
        });
      } else if (type == 2) {
        axios.get("/api/index/courseware/get?id=" + id).then((d) => {
          this.currpage = "detail1";
          this.article1 = d.data.data;
          console.log(this.article1);
        });
      }
    },
  },
  watch: {
    $route(to, from) {
      this.loadData(this.menu[to.query.index]);
    },
  },
};
</script>
<style scoped>
.txt video {
  width: 100%;
}
.fileDetailImg {
  width: 75%;
  height: auto;
  text-align: center;
}
.Flink h3 {
  color: #333;
}
.fileLink {
  margin: 0 0 0 2em;
}
.fileLink:hover {
  text-decoration: underline;
}
</style>


